import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import bridge from '@vkontakte/vk-bridge';
import { Header, PanelHeaderButton, platform, IOS, ActionSheetItem, ActionSheet } from '@vkontakte/vkui';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon28Profile from '@vkontakte/icons/dist/28/profile';
import Icon28CameraOutline from '@vkontakte/icons/dist/28/camera_outline';

import './Persik.css';

const osName = platform();

		async function fetchData() {
			let user = await bridge.send('VKWebAppGetUserInfo');
			this.setState({ fetchedUser: user });
		}
		setInterval(() => {
			fetchData();
		}, 250)

const Info = props => (
	<Panel id={props.id}>
			<PanelHeader
				left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
			></PanelHeader>	
		<Group header={<Header mode="secondary">Аккаунт</Header>}>
			<Cell
				description={`Ваш баланс 1000`}
			>
				{`${props.fetchedUser}`}
			</Cell>
			<Cell>
			<Button style={{ marginRight: 8 }} mode="commerce" target="_blank" href="https://vk.com/app7349811#merchant456906612_1">Пополнить</Button>
			<Button mode="destructive" onClick={props.openBase}>Вывести</Button>
			</Cell>
	     </Group>
		<Group header={<Header mode="secondary">Музыка</Header>}>
		</Group>
		<div class="btn-ogan-vlad-lox"><a href="https://vk.com/vivcharovanton" target="_blank" class="btn-ogan link">Создатель</a></div>
	</Panel>
);

Info.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Info;
